@import '../../sass/colors';
@import '../../sass/fonts';
@import '../../sass/mixins';

.header {
  @include background-gradient;
  min-height: 80px;
  display: flex;
  align-items: flex-end;
  margin: 0;
  padding: 10px 20px;
}

.h1 {
  font-family: $font-helvetica;
  font-size: 12px;
  color: $white;
  letter-spacing: 1.5px;
  text-align: center;
  text-transform: uppercase;
  flex: auto;
  margin: 0 5px;
}

.contentWrapper {
  flex: auto;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
}
