@import '../../sass/colors';
@import '../../sass/fonts';
@import '../../sass/mixins';

.userCard {
  margin-bottom: 15px;
}

.visible {
  background: $white;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.1);
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  padding: 20px;
  text-transform: capitalize;
  font-family: $font-helvetica;
}

.userImage {
  height: 60px;
  width: 60px;
  border-radius: 50%;
  margin-right: 25px;
}

.userInfoWrapper {
  flex: auto;
  padding: 10px;
}

.userName {
  font-size: 16px;
  color: $bitter-sweet;
  margin-bottom: 5px;
}

.userBirthdate {
  font-family: $font-helvetica;
  font-size: 16px;
  color: $jumbo;
}

.userLocation {
  font-size: 12px;
  color: $jumbo;
}

.expandableContainer {
  @include background-gradient;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: 0 auto 10px;
  max-height: 0;
  transition: max-height 600ms;
  overflow: hidden;

  @media (min-width: 255px) {
    flex-direction: row;
    flex-wrap: wrap;
    align-items: stretch;
    justify-content: center;
  }
}

.expanded {
  max-height: 500px;
  transition: max-height 600ms;
}

.expandableBox {
  font-family: $font-helvetica;
  font-size: 12px;
  color: $white;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex: 0 0 41%;
  padding: 10px;
  border-bottom: 1px solid $mandy;

  &:first-child {
    @media (min-width: 255px) {
      border-bottom: none;
      border-right: 1px solid $mandy;
    }
  }
}

.expandableTitle {
  font-size: 14px;
  margin-bottom: 10px;
  text-transform: capitalize;
}

.expandableDescription {
  word-break: break-word;
}

.arrow {
  border: solid $zircon;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
}

.arrowUp {
  transform: rotate(-135deg);
  -webkit-transform: rotate(-135deg);
}

.arrowDown {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
